body {
  margin: 0;
  padding: 0;

  font-family: 'Open Sans', sans-serif;
  color: #000;
  background-color: #eaeef3;
}

.App {
  display:  flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
}

.App-header {
  font-weight: 400;
  text-transform: uppercase;
  margin: 40px 0 30px;
  padding: 0;
}

.loader {
  width: 200px;
  height: 200px;
  border-radius: 50%;

  background: url(./images/plane.gif) no-repeat center;
  background-size: cover;

}

.filter {
  box-sizing: border-box;
  padding: 20px;
  margin: 25px 0;
  border: none;
  text-transform: uppercase;

  width: 100%;
  background-color: #f7f7f7;
  box-shadow: 0 2px 5px rgba(136, 136, 136, 0.1);
  transition: box-shadow 0.4s;
}

.filter:hover {
  background-color: #ffffff;
  box-shadow: 0 2px 10px rgba(9, 8, 61, 0.2);
}

.button-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.button {
  width: 50%;

  border: none;
  padding: 20px 0;

  background-color: #ffffff;

  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.6s;
}

.button--active {
  color: #fff;
  background-color: #096fd4;
}

.button:hover {
  color: #fff;
  background-color: #43cea2 ;
}

.button--active:hover {
  color: #fff;
  background-color: #096fd4;
}

.flights-table {
  border-collapse:separate;
  border-spacing: 0 8px;
}

.flights-table__body tr {
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(136, 136, 136, 0.1);

  transition: all 0.7s;
}

.flights-table__body tr:nth-child(even) {
  background-color: #f7f7f7;
}

.flights-table__body tr:hover {
  transform: scale(1.01)
}

.flights-table th {
  text-align: left;
  padding: 20px 15px 10px;
}

.flights-table td {
  padding: 10px 15px;
}

.flights-table__terminal {
  display: block;
  color: #fff;
  text-align: center;
  background-color: #096fd4;
}

.flights-table__logo-cell {
  display: flex;
  align-items: center;
}

.flights-table__logo-cell img {
  margin-right: 5px;
}

.flights-table__logo-cell p {
  margin: 0;
  padding: 0;
}

.flights-table__link-cell a {
  color: #185a9d;

  opacity: 0;
  transition: all 0.7s;
}

.flights-table__body tr:hover a {
  opacity: 1;
}

.flights-table__body tr a:hover {
  color: #43cea2;
}

.header-cell__title-wrapper {
  font-size: 15px;
  font-weight: bold;

  min-height: 24px;
  position: relative;
  padding-right: 20px;
  cursor: pointer;

  background-color: transparent;
  border: none;

  transition: all 0.4s;
}

.header-cell__title-wrapper:hover {
  color: #43cea2;
}

.header-cell__title-wrapper:before {
  content: '';
  position: absolute;
  right: 5px;
  top: 4px;

  width: 0;
  height: 0;

  border-style: solid;
  border-width: 0 4px 6px 4px;
  border-color: transparent transparent #185a9d transparent;
  transition: all 0.4s;
}

.header-cell__title-wrapper--unsort:before {
  content: '';
  position: absolute;
  left: 1px;
  top: 6px;
  border: none;
  opacity: 0.3;

  width: 16px;
  height: 16px;

  background-size: contain;
  background-image: url(./images/refresh.svg);
  transition: all 0.5s;
}

.header-cell__title-wrapper--unsort:hover:before {
  opacity: 1;
}

.header-cell__title-wrapper:hover:before {
  border-color: transparent transparent #43cea2 transparent;
}

.header-cell__title-wrapper:after {
  content: '';
  position: absolute;
  right: 5px;
  bottom: 4px;

  width: 0;
  height: 0;

  border-style: solid;
  border-width: 6px 4px 0 4px;
  border-color: #185a9d transparent transparent transparent;
  transition: all 0.4s;
}

.header-cell__title-wrapper--unsort:after {
  border: none;
}

.header-cell__title-wrapper--hidden {
  display: none;
}

.header-cell__title-wrapper:hover:after {
  border-color: #43cea2 transparent transparent transparent
}
